import React, { Component } from "react";
import Slider from "react-slick";

import "slick-carousel/slick/slick.css";

import "slick-carousel/slick/slick-theme.css";


export default class Responsive extends Component {
    render() {
        var settings = {
            dots: false,
            infinite: true,
            autoplay: true,
            speed: 2500,
            autoplaySpeed: 300,
      
            slidesToShow: 5,
            slidesToScroll: 1,
            initialSlide: 0,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1,
                        infinite: true,
                        dots: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        initialSlide: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
            ]
        };
        return (
            <div style={{ width: "90%", margin: "50px auto" }}>
                <h2 style={{ margin: "20px" }}> Our Partners </h2>
                <Slider {...settings}>
                    <div>
                        <img src="assets/img/zomato.png" alt="zomato" />
                    </div>
                    <div>
                        <img src="assets/img/byjus.png" alt="byjus" />
                    </div>
                    <div>
                        <img src="assets/img/dunzo.png" alt="dunzo" />
                    </div>
                    <div>
                        <img src="assets/img/airtel.png" alt="airtel" />
                    </div>
                    <div>
                        <img src="assets/img/uber.png" alt="uber" />
                    </div>

                </Slider>
            </div>
        );
    }
}